body {
  margin: 0;
  padding: 0;
  font-size: 1em;
  line-height: 1.5;
  font-weight: 300;
  color: #111;
  font-family: "Miriam Libre", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: url("../images/background.svg") #edeff0; }

h1, h2 {
  font-family: "Miriam Libre", "Helvetica Neue", Helvetica, Arial, sans-serif; }

p {
  max-width: 350px; }

article, p {
  font-family: "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 35px auto 0;
  line-height: 2em;
  text-align: justify;
  font-weight: 400;
  letter-spacing: 2px; }

article {
  width: 80%;
  color: #111; }
  article h1 {
    text-align: center; }

ol li {
  color: #111; }

header nav {
  padding-bottom: 50px;
  text-align: left; }

header nav ul li {
  display: inline-block;
  margin-right: 20px; }

header nav ul li a svg {
  height: 13px;
  width: 13px;
  transition: height 0.4s cubic-bezier(0.41, -0.32, 0.33, 1.71); }

header nav ul li a {
  text-decoration: none;
  color: #52595f;
  font-weight: 700;
  padding: 1em;
  line-height: 1.5em; }
  header nav ul li a:hover {
    color: #111; }
  header nav ul li a:hover svg {
    fill: #111;
    height: 8px; }

.gallery {
  padding-top: 50px;
  width: 90%;
  margin: auto; }
  .gallery h2 {
    font-size: 28px; }
  .gallery ul {
    padding: 0; }

.gallery__item {
  display: inline-block;
  color: #BA8058;
  margin-right: 20px; }
  .gallery__item:last-child {
    margin-right: 0; }
  .gallery__item svg {
    margin-bottom: 15px;
    width: 170px;
    height: 170px; }
  .gallery__item span {
    margin-left: -40px; }

.icons--corgi-red {
  --default-color: #BA8058;
  --primary-color: #FBFEFF;
  --secondary-color: #2D3339;
  --tertiary-color: #EAAA7F; }

.icons--corgi-fawn {
  --default-color: #EAAA7F;
  --primary-color: #FBFEFF;
  --secondary-color: #2D3339;
  --tertiary-color: #F4C6A6; }

.icons--corgi-sable {
  --default-color: #844B24;
  --primary-color: #FBFEFF;
  --secondary-color: #2D3339;
  --tertiary-color: #BA8058; }

.icons--corgi-blacktan {
  --default-color: #BA8058;
  --primary-color: #52595F;
  --secondary-color: #FBFEFF;
  --tertiary-color: #2D3339; }

.title-h1 {
  color: #BA8058;
  margin-bottom: 2em; }
  @media (min-width: 425px) {
    .title-h1 {
      display: none; } }

.icons--logo {
  --default-color: #ba8058;
  --primary-color: #fff;
  --secondary-color: #f4c6a6; }
  @media (min-width: 425px) {
    .icons--logo {
      --primary-color: #eaaa7f; } }

header {
  padding-top: 10px; }
  header h1 {
    line-height: 1em; }

.container {
  max-width: 960px;
  background: #fff;
  margin: 1em auto;
  box-shadow: 0 2px 1px 1px #a09f9e;
  border-radius: 5px; }

.wrapper {
  margin: auto;
  display: block; }

footer {
  width: 100%;
  margin-top: 100px;
  padding-bottom: 30px;
  color: #BA8058; }
